// utils.js
export const formatDate = (timestamp) => {
    if (!timestamp) return "";
  
    const date = new Date(timestamp);
    const day = date.getDate().toString().padStart(2, '0');
    const month = (date.getMonth() + 1).toString().padStart(2, '0'); 
    const year = date.getFullYear();
  
    return `${day}/${month}/${year}`; 
  };

  export function isMobile() {
    if (window) {
      return window.matchMedia(`(max-width: 767px)`).matches;
    }
    return false;
  }
  

  export const convertTimeToDate = (timestamp) => {
    if (!timestamp) return "";
  
    const date = new Date(timestamp); // To i tng Date t timestamp
  
    const year = date.getFullYear();
    const month = (date.getMonth() + 1).toString().padStart(2, "0"); // Ly thng (thm 1 v thng bt u t 0) v thm 0 vo u nu cn
    const day = date.getDate().toString().padStart(2, "0"); // Ly ngy v thm 0 vo u nu cn
  
    return `${year}-${month}-${day}`; // Tr v chui nh dng YYYY-MM-DD
  };